main {
  margin: 0 auto;
  width: 960px;
  border: 5px solid #0a1303;
}

.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.cell {
  width: 10px;
  height: 10px;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 1px solid #0a1303;
}
.cell.dead {
  background-color: rgba(15, 28, 4, 0.9);
}
.cell.live {
  background-color: #377c00;
}
.cell.birth {
  background-color: #b6ff7c;
}

.controls {
  width: 100%;
  height: 48px;
  border-top: 5px solid #0a1303;
  background-color: #0a1303;
}



